h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

body {
  background-color: var(--white);
  font-family: DMSans-400, sans-serif;
}

.visually-hidden {
  white-space: nowrap;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

html {
  box-sizing: border-box;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.link {
  text-decoration: none;
}

.container {
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 375px) {
  .container {
    max-width: 100%;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 100%;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    max-width: 100%;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@font-face {
  font-family: DMSans-400;
  src: url("DMSans-Regular.23c41658.ttf");
}

@font-face {
  font-family: DMSans-400-italic;
  src: url("DMSans-Italic.b1eebbd6.ttf");
}

@font-face {
  font-family: DMSans-500;
  src: url("DMSans-Medium.0957dae6.ttf");
}

@font-face {
  font-family: DMSans-700;
  src: url("DMSans-Bold.51beb29d.ttf");
}

:root[data-theme="light"] {
  --deep-black: #111;
  --soft-black: #202024;
  --purple: #4f2ee8;
  --yellow: #eac645;
  --white: #fff;
  --backdrop-color: #1116;
  --purple-gradient: radial-gradient(183.6% 183.01% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  --switch-circle-pos: 2px;
  --theme-switcher-gradient: linear-gradient(#4f2ee8, #fff);
}

:root[data-theme="dark"] {
  --deep-black: #fff;
  --soft-black: #202024;
  --purple: #eac645;
  --yellow: #4f2ee8;
  --white: #202024;
  --backdrop-color: #1116;
  --purple-gradient: radial-gradient(183.6% 183.01% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  --switch-circle-pos: 17px;
  --theme-switcher-gradient: linear-gradient(180deg, #4f2ee8 0%, #686868 100%);
}

:root {
  --regular: font-weight: 400;
  --medium: font-weight: 500;
  --bold: font-weight: 700;
  --function-duration: .35s;
  --function-timing: cubic-bezier(.4, 0, .2, 1);
}

.header-container {
  border: 2px solid var(--deep-black);
  z-index: 3;
  background-color: var(--white);
  border-radius: 0 0 8px 8px;
  flex-direction: row;
  align-items: center;
  width: 100%;
  height: 24px;
  margin: 0 auto;
  padding: 18px 20px;
  display: flex;
  position: relative;
  top: 0;
}

.user-photo-img {
  border-radius: 50%;
  width: 37px;
  height: 37px;
  margin-right: 12px;
}

.logo {
  background: var(--icon-logo);
  flex-direction: row;
  align-items: center;
  gap: 8px;
  width: 109px;
  height: 28px;
  margin-right: auto;
  font-weight: 600;
  display: flex;
}

.header-title {
  font-family: DMSans-400, sans-serif;
}

.header-wrap {
  flex-direction: row;
  align-items: center;
  display: flex;
}

.theme-switcher {
  background-image: var(--theme-switcher-gradient);
  cursor: pointer;
  border-radius: 50px;
  align-items: center;
  width: 39px;
  height: 22px;
  margin-right: 14px;
  padding: 1px 0 1px 1px;
  display: flex;
  position: relative;
}

.theme-switcher:before {
  content: "";
  transform: translateX(var(--switch-circle-pos));
  background-color: #fff;
  border-radius: 50%;
  width: 18px;
  height: 18px;
  transition: transform .25s;
  position: absolute;
  box-shadow: 1px 1px 2px #0000001a;
}

.mob-menu-open, .mob-menu-close {
  stroke: var(--deep-black);
  background-color: #0000;
  border: none;
  justify-content: center;
  align-items: center;
  width: 28px;
  height: 28px;
  padding: 0;
  display: flex;
}

.sign-up-btn, .header-nav {
  display: none;
}

.username {
  justify-content: center;
  width: 120px;
  height: 29px;
  font-family: DMSans-700, sans-serif;
  font-size: 22px;
  line-height: 28.64px;
}

.mob-menu {
  z-index: 2;
  background-image: linear-gradient(#4f2ee8, #fff);
  background-position: bottom;
  background-repeat: repeat-x;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  display: flex;
  position: absolute;
  top: 0;
  overflow-y: auto;
}

.mob-menu-wrap {
  flex-direction: column;
  display: flex;
  overflow-y: auto;
}

.user-profile {
  flex-direction: row;
  align-items: center;
  width: 133px;
  height: 37px;
  margin-top: 48px;
  display: flex;
}

.mob-menu-sign-up-btn {
  cursor: pointer;
  color: #fff;
  z-index: 999;
  background-color: #0000;
  border: none;
  align-items: center;
  width: 87px;
  height: 29px;
  margin-left: 20px;
  padding: 0;
  display: flex;
  position: relative;
}

.user-photo {
  stroke: #fff;
}

.mob-menu-nav {
  flex-direction: column;
  row-gap: 24px;
  margin: 126px auto 0;
  display: flex;
}

.mob-menu-nav-item-home {
  justify-content: center;
  align-items: center;
  width: 72px;
  height: 34px;
  list-style: none;
  display: flex;
}

.mob-menu-nav-item-shopping-list {
  justify-content: center;
  align-items: center;
  width: 156px;
  height: 34px;
  list-style: none;
  display: flex;
}

.mob-menu-home-link {
  color: #fff;
  font-family: DMSans-500, sans-serif;
  font-size: 14px;
  line-height: 18px;
  text-decoration: none;
}

.mob-menu-shopping-list-link {
  color: #fff;
  flex-direction: row;
  align-items: center;
  gap: 4px;
  font-family: DMSans-500, sans-serif;
  font-size: 14px;
  line-height: 18px;
  text-decoration: none;
  display: flex;
}

.mob-menu-log-out-btn {
  color: #111;
  background-color: #fff;
  border: 1.5px solid #111;
  border-radius: 18px;
  justify-content: center;
  align-items: center;
  gap: 4px;
  width: 118px;
  height: 48px;
  margin-bottom: 24px;
  margin-left: 20px;
  font-family: DMSans-500, sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
  display: flex;
}

.mob-menu-log-out-btn svg {
  width: 20px;
  height: 20px;
}

.mob-menu-img {
  z-index: -1;
  position: absolute;
  bottom: 0;
}

.visually-hidden {
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.no-scroll {
  overflow: hidden;
}

.is-active {
  background-color: #eac645;
  border-radius: 36px;
  font-weight: 700;
}

.is-active a {
  color: #111;
}

.null {
  display: none;
}

@media screen and (min-width: 375px) {
  .mob-menu-wrap, .mob-menu-log-out-wrap {
    width: 375px;
    margin: 0 auto;
  }

  .mob-menu-img {
    display: none;
  }
}

@media screen and (min-width: 768px) {
  .header-container {
    width: 768px;
    height: 72px;
    margin: 0 auto;
    position: static;
  }

  .mob-menu-open, .mob-menu-close, .mob-menu {
    display: none;
  }

  .logo {
    margin-left: 32px;
    margin-right: 40px;
  }

  .header-nav {
    flex-direction: row;
    align-items: center;
    gap: 16px;
    margin-right: auto;
    list-style: none;
    display: flex;
  }

  .header-nav-item-home {
    justify-content: center;
    align-items: center;
    width: 72px;
    height: 34px;
    display: flex;
  }

  .header-nav-item-shopping-list {
    justify-content: center;
    align-items: center;
    width: 156px;
    height: 34px;
    display: flex;
  }

  .header-home-link {
    color: var(--deep-black);
    font-family: DMSans-500, sans-serif;
    font-size: 14px;
    line-height: 18px;
    text-decoration: none;
  }

  .header-shopping-list-link {
    color: var(--deep-black);
    flex-direction: row;
    align-items: center;
    gap: 4px;
    font-family: DMSans-500, sans-serif;
    font-size: 14px;
    line-height: 18px;
    text-decoration: none;
    display: flex;
  }

  .theme-switcher {
    margin-right: 24px;
  }

  .sign-up-btn {
    color: #fff;
    background-color: #4f2ee8;
    border: none;
    border-radius: 36px;
    align-items: center;
    width: 184px;
    height: 45px;
    margin-right: 32px;
    display: flex;
  }

  .sign-up-btn svg {
    margin-left: 4px;
  }
}

@media screen and (min-width: 1440px) {
  .header-container {
    width: 1440px;
  }

  .logo {
    margin-right: 76px;
  }
}



body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

.container {
  flex-direction: column;
  align-items: flex-start;
  width: 335px;
  height: 226px;
  display: flex;
  overflow: hidden;
}

.categories-2 {
  flex-direction: column;
  align-items: flex-start;
  gap: 24px;
  margin-top: 40px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  position: relative;
  overflow-y: auto;
}

.categories-title {
  color: #1119;
  letter-spacing: -.36px;
  margin-left: 20px;
  font-family: DMSans-400;
  font-size: 18px;
  font-style: normal;
  line-height: 24px;
}

.categories-list {
  width: 100%;
  margin: 48px 0 0;
  padding: 8px 0 10px;
  list-style: none;
  overflow: auto;
}

.categories-el, .categories-btn, .categories-list li {
  color: #1119;
  letter-spacing: -.32px;
  white-space: normal;
  text-overflow: ellipsis;
  cursor: pointer;
  justify-content: flex-start;
  align-items: center;
  gap: 8px;
  max-width: 100%;
  font-family: DMSans-400;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  list-style: none;
  transition: color .3s, font-size .3s;
  display: flex;
  overflow: hidden;
}

.categories-el:hover, .categories-btn:hover, .categories-list li:hover {
  color: #4f2ee8;
  letter-spacing: -.36px;
  text-transform: uppercase;
  font-family: DMSans-700;
  font-size: 18px;
  font-style: normal;
  line-height: 24px;
}

.categories-btn {
  z-index: 1;
  position: absolute;
  top: 0;
  left: 0;
}

@media (min-width: 320px) and (max-width: 767px) {
  .categories-el {
    white-space: normal;
    text-overflow: ellipsis;
    max-width: 100%;
    margin-bottom: 24px;
    font-size: 16px;
    overflow: hidden;
  }

  .categories-el:hover, .categories-list li:hover {
    max-width: calc(100% - 16px);
    font-size: 16px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    flex-direction: column;
    align-items: flex-start;
    gap: 32px;
    width: 309px;
    height: 472px;
    margin-left: 32px;
    display: flex;
  }

  .categories-title {
    margin-left: 20px;
  }

  .categories-el {
    margin-bottom: 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    flex-direction: column;
    align-items: flex-start;
    gap: 32px;
    width: 328px;
    display: flex;
  }

  .categories-el {
    color: #1119;
    letter-spacing: -.36px;
    margin-bottom: 32px;
    font-family: DMSans-400;
    font-size: 18px;
    font-style: normal;
    line-height: 24px;
  }

  .categories-list {
    width: 100%;
    padding: 0;
    list-style: none;
    overflow: auto;
  }

  .categories-el:hover, .categories-list li:hover {
    color: #4f2ee8;
    letter-spacing: -.36px;
    text-transform: uppercase;
    font-family: DMSans-700;
    font-size: 18px;
    font-style: normal;
    line-height: 24px;
  }
}

.support-section {
  padding-top: 40px;
  padding-bottom: 40px;
}

.charity-container {
  width: 375px;
  margin: 0 auto;
  padding-inline: 20px;
}

.charity-box {
  padding-inline: 40px;
  background: radial-gradient(189.32% 190.93% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  border-radius: 16px;
  width: 100%;
  padding-top: 24px;
  padding-bottom: 20px;
  position: relative;
  box-shadow: 0 10px 10px #f5f5f71a;
}

.charity-header {
  gap: 12px;
  margin-bottom: 40px;
  display: flex;
}

.charity-title {
  color: #fff;
  letter-spacing: -1.12px;
  font-family: DMSans-700;
  font-size: 28px;
  line-height: 1.14;
  font-weight: var(--bold);
}

.swiper-slide:before {
  counter-increment: section;
  content: "0" counter(section) " ";
  color: #fff;
  font-family: DMSans-400;
  font-size: 14px;
  font-weight: var(--regular);
  letter-spacing: -.56px;
  line-height: 1.29;
}

.swiper {
  height: 188px;
  margin-bottom: 48px;
  overflow: hidden;
}

.swiper-wrapper {
  counter-reset: section;
  width: 100%;
  height: 100%;
  margin-bottom: 16px;
  list-style-type: none;
}

.swiper-slide {
  align-items: center;
  gap: 16px;
  width: 50%;
  font-family: DMSans-400;
  font-size: 18px;
  display: flex;
}

.charity-link {
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
}

.charity-img {
  filter: brightness(0) invert();
  max-width: -moz-fit-content;
  max-width: fit-content;
  max-height: 100%;
}

.swiper-button-next, .swiper-button-up {
  z-index: 0;
  background-color: #fff;
  border: none;
  border-radius: 38px;
  outline: none;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  display: flex;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.swiper-button-next-icon, .swiper-button-up-icon {
  fill: #4f2ee8;
}

.swiper-button-next-icon {
  transform: rotate(180deg)translateY(-105%)translateX(-105%);
}

.swiper-button-up.is-hidden {
  display: none;
}

@media screen and (max-width: 375px) {
  .charity-container {
    width: 100%;
  }

  .charity-title {
    font-size: 25px;
  }
}

@media screen and (min-width: 768px) {
  .swiper-button-next, .swiper-button-up {
    width: 38px;
    height: 38px;
  }

  .charity-box {
    padding-bottom: 14px;
  }

  .swiper-wrapper {
    margin-bottom: 32px;
  }

  .swiper {
    height: 292px;
    margin-bottom: 70px;
  }
}

@media screen and (min-width: 1440px) {
  .charity-container {
    width: 396px;
  }

  .swiper-wrapper {
    margin-bottom: 40px;
  }

  .charity-box {
    padding-bottom: 24px;
  }

  .swiper {
    margin-bottom: 78px;
  }
}

