.go-top {
  cursor: pointer;
  background-color: var(--main-accent-color);
  z-index: 1000;
  border-radius: 50%;
  width: 54px;
  height: 54px;
  padding: 0;
  display: none;
  position: fixed;
  bottom: 25px;
  right: 25px;
}

.go-top-icon {
