h2 {
  margin: 0;
}
.book-image {
  border-radius: 8px;
  margin-bottom: 14px;
}

.book-title {
  margin-bottom: 40px;
}
.book-card {
  display: flex;
  flex-direction: column;
}

.category-heading {
  display: block;
  font-family: DMSans-400;
  font-size: 14px;
  font-weight: var(--regular);
  line-height: 1.3;
  margin-bottom: 40px;
}
@media screen and (min-width: 375px) {
  .book-card:not(:first-child) {
    display: none;
  }
  .category-block {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    justify-content: flex-start;
    align-items: flex-start;
  }
  .book-list {
    max-width: 335px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

@media screen and (min-width: 768px) {
  .book-card:nth-child(-n + 3) {
    display: block;
  }
  .book-list {
    max-width: 704px;
  }
  .category-block {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .book-image {
    width: 218px;
    height: 316px;
  }
}

@media screen and (min-width: 1440px) {
  .book-card:nth-child(-n + 5) {
    display: block;
  }
  .book-list {
    max-width: 996px;
  }
}
.book-list {
  font-family: DM Sans;
  font-size: 32px;
  font-weight: 700;
  line-height: 1.2;
  letter-spacing: -0.04em;
  text-align: left;
}

.books-design {
  color: var(--purple);
}
